@use "@pkg/react-storybook/Component";
@use "@app/bancor-components/Element/Text";
@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Font";
@use "@app/bancor-components/Style/Color";
@use "@app/bancor-components/Style/Border";

$name: Link;
$type: Component.$type-element;
$selector: Component.selector($name, $type);

@mixin asButton {
    @include Color.set($bg: primary);
    @include Font.set($deco: default, $size: beta);
    @include Border.set($radius: iota);
    @include Flex.set($justify: center);
    cursor: pointer;
    transition: background-color 0.8s ease;
    width: Layout.get-max-width(alpha);

    #{Text.$selector} {
        @include Color.set($fg: white);
    }

    &:hover {
        @include Color.set($bg: "primary-variant-100");

        #{Text.$selector} {
            @include Color.set($fg: primary);
        }
    }
}

@mixin asButtonWhite {
    @include asButton;
    @include Color.set($bg: white, $fg: primary);

    #{Text.$selector} {
        @include Font.set($align: default);
        @include Color.set($fg: primary);
        @include Margin.set(alpha);
    }
}

:export {
    NAME_LINK: $name;
    TYPE_LINK: $type;
}
