@use "." as this;
@use "@app/bancor-components/Style/screen";
@use "@app/bancor-components/Style/Border";
@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Layout/Padding";
@use "@app/bancor-components/Style/Layout/Position";

$container-cards-height: 135px;
$image-cards-height: 100px;
$next-prev-buttons-position: calc(50% - (var(--button-medium-size) / 2));

#{this.$selector} {
    .loadingCard {
        width: Layout.get-max-width(alpha);
        height: $container-cards-height;
    }
    img:-moz-broken {
        -moz-force-broken-image-icon: 1;
        width: auto;
    }

    div[role="list"] {
        @include Margin.set($preset: none);

        > div {
            @include Padding.set($preset: none);
            width: Layout.get-width(kappa);

            img {
                @include Border.set($radius: delta);
                object-fit: fill;

                @include screen.md {
                    object-fit: cover;
                }
            }
            
            a {
                display: inline-block;
            }
        }
    }
    nav {
        div[data-key="prev"] {
            @include Position.set($left: beta, $top: $next-prev-buttons-position);
        }

        div[data-key="next"] {
            @include Position.set($left: auto, $top: $next-prev-buttons-position, $right: beta);
        }
    }
}
