@use "." as this;
@use "@app/bancor-components/Element/Link";
@use "@app/bancor-components/Element/Text";
@use "@app/bancor-components/Element/Image";
@use "@app/bancor-components/Style/screen";
@use "@app/bancor-components/Style/Color";
@use "@app/bancor-components/Style/Font";
@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Layout/Position";

$card-height: 149px;
$max-width-image: 180px;
$width-image: calc(40% + max(Layout.get-size(beta), 2.5vw));

@mixin textFormat {
    @include Font.set($family: default, $weight: default);
    @include Color.set($fg: "primary-contrast");
    line-height: 16px;
}
@mixin truncate {
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
}

#{this.$selector} {
    @include Flex.set($align: center, $dir: row);
    @include Color.set($bg: "primary");
    @include Margin.set($top: epsilon, $bottom: delta);
    @include Position.set($position: relative);
    height: $card-height;

    @include screen.xl {
        min-height: $card-height;
        height: auto;
    }

    .information {
        @include Flex.set($justify: between, $dir: column, $align: start);
        min-height: calc($card-height - Layout.get-space(delta));

        #{Text.$selector}.text {
            @include textFormat;
            @include truncate;
            -webkit-line-clamp: 5;
            span {
                & > * {
                    @include Margin.set($bottom: alpha);
                }
                & > :first-child {
                    @include Font.set($size: beta);
                }
            }
        }
        & > footer {
            @include Margin.set($top: alpha);
            .chatButton {
                #{Link.$selector} {
                    @include Link.asButtonWhite;
                    #{Text.$selector} {
                        @include truncate;
                        @include Margin.set($left: beta, $right: beta);
                        @include Font.set($weight: medium);
                        -webkit-line-clamp: 1;
                    }
                }
            }
        }
    }

    .wrapperImage {
        @include Flex.set($align: center, $dir: row);
        flex: 1 0 calc($width-image - 5%);

        @include screen.xl {
            flex: 1 0 calc($width-image - 10%);
        }

        #{Image.$selector} {
            @include Position.set($position: absolute, $bottom: none, $right: none);
            right: -2%;
            width: $width-image;
            max-width: $max-width-image;
        }
    }

}
