@use "." as this;
@use "@app/bancor-components/Style/screen";
@use "@app/bancor-components/Style/Color";
@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Layout/Display";
@use "@app/bancor-components/Style/Layout/Padding";
@use "@app/bancor-components/Element/Widgets/Pending";
@use "@app/bancor-components/Element/Widgets/Recirculation";
@use "@app/bancor-components/Element/Card/Chat";

.Desktop {
    @include Display.set($display: grid);
    @include Padding.set($top: beta, $right: alpha, $left: alpha, $bottom: delta);

    [role="separator"] {
        @include Color.set($bg: "gray-400");
        @include Margin.set($top: delta, $bottom: delta);
        width: auto;
    }

    &.fetching {
        @include Flex.set($justify: center);
    }

    &.widgetInactive {
        @include Margin.set($left: auto, $right: auto);
    }

    &.LayoutWidgetsCorporate {
        @include Padding.set($right: beta, $left: beta);
        grid-template-areas: "authorizations" "actionsAndCampaigns" "recirculation" "chat";

        .firstWidgetsColumnCorporate {
            grid-area: actionsAndCampaigns;
        }

        #{Pending.$selector} {
            grid-area: authorizations;
        }

        #{Recirculation.$selector} {
            grid-area: recirculation;
        }

        #{Chat.$selector} {
            grid-area: chat;
        }
    }

    &.LayoutWidgetsRetail {
        grid-template-areas: "actionsAndCampaigns" "authorizations";

        .firstWidgetsColumnRetail {
            @include Padding.set($top: beta, $right: alpha, $left: alpha);
            grid-area: actionsAndCampaigns;
            overflow-x: hidden;
        }

        #{Recirculation.$selector} {
            grid-area: authorizations;
        }
    }

    @include screen.xl {
        @include Padding.set($top: gamma, $right: gamma, $left: gamma);
        grid-row: 2;
        grid-column: 2;
        grid-template-columns: 2fr 1fr;
        grid-auto-rows: max-content;
        gap: Layout.get-space(beta);

        &.LayoutWidgetsRetail {
            grid-template-areas: "actionsAndCampaigns authorizations";
        }

        &.LayoutWidgetsCorporate {
            @include Padding.set($right: delta, $left: delta);
            grid-template-areas: "actionsAndCampaigns authorizations" "actionsAndCampaigns recirculation" " actionsAndCampaigns chat";
        }

        section {
            [role="separator"]:last-child {
                @include Display.set($display: none);
            }
        }

        & > section {
            @include Padding.set($top: alpha, $right: alpha, $left: alpha);
            overflow: hidden;
        }
    }
}
