@use "." as this;
@use "@app/bancor-components/Style/screen";
@use "@app/bancor-components/Style/size";
@use "@app/bancor-components/Element/Text";
@use "@app/bancor-components/Element/Link";
@use "@app/bancor-components/Element/Button";
@use "@app/bancor-components/Element/Image";
@use "@app/bancor-components/Style/Color";
@use "@app/bancor-components/Style/Font";
@use "@app/bancor-components/Style/Border";
@use "@app/bancor-components/Style/Shadow";
@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Layout/Padding";
@use "@app/bancor-components/Style/Layout/Position";
@use "@app/bancor-components/Style/Layout/Display";

$icon-actions-min-width: 152px;
$icon-actions-min-width-xs: 96px;
$pos-badge: 0.625rem;

#{this.$selector} {
    @include Padding.set($preset: beta);
    @include Border.set($radius: gamma);
    @include Color.set($bg: "white");
    @include Shadow.set($box: alpha);

    & > nav {
        & > ul {
            @include Display.set($display: grid);
            @include Font.set($align: default);
            grid-template-columns: repeat(auto-fit, minmax($icon-actions-min-width-xs, 1fr));

            @include screen.sm {
                grid-template-columns: repeat(3, 1fr);
            }

            & > li {
                @include Margin.set($preset: alpha);
                min-width: Layout.get-space(eta);

                &.disabled {
                    pointer-events: none;
                    opacity: var(--color-opacity-06);
                }

                .bag {
                    @include Color.set($bg: "transparent");
                    @include Padding.set($preset: none);
                    @include Border.set($style: default);
                    cursor: pointer;
                    width: 100%;
                }
                .wrapperAvatar {
                    @include Position.set($position: relative);

                    #{Image.$selector} {
                        @include Image.asModoBadge;
                        top: - $pos-badge;
                        right: - $pos-badge;
                    }
                }

                #{Link.$selector},
                #{Button.$selector} {
                    @include Flex.set($align: center, $dir: column);

                    #{Text.$selector} {
                        @include Display.set($display: block);
                        @include Padding.set($top: alpha);
                        @include Font.set(
                            $size: gamma,
                            $weight: normal,
                            $family: "default",
                            $align: default
                        );
                    }
                }
            }
        }
    }

    @include screen.xl {
        nav {
            & > ul {
                grid-template-columns: repeat(auto-fit, minmax($icon-actions-min-width, 1fr));
            }
        }
    }
}
